ype')\"\n                    required\n                    outlined\n                    dense\n                    :error=\"errors.type ? true : false\"\n                    :error-messages=\"errors.type\"\n                ></v-select>\n                <v-menu\n                    v-model=\"dateMenu\"\n                    :close-on-content-click=\"false\"\n                    :nudge-right=\"40\"\n                    transition=\"scale-transition\"\n                    offset-y\n                    min-width=\"auto\"\n                >\n                    <template v-slot:activator=\"{ on, attrs }\">\n                        <v-text-field\n                            v-model=\"date\"\n                            :label=\"$t('date')\"\n                            outlined\n                            dense\n                            clearable\n                            readonly\n                            v-bind=\"attrs\"\n                            v-on=\"on\"\n                            :error=\"errors.date ? true : false\"\n                            :error-messages=\"errors.date\"\n                        ></v-text-field>\n                    </template>\n                    <v-date-picker\n                        v-model=\"date\"\n                        @input=\"dateMenu = false\"\n                        type=\"month\"\n                    ></v-date-picker>\n                </v-menu>\n                <CButton @click=\"submit\" color=\"primary\" class=\"px-4\">\n                    {{ $t(\"button.submit\") }}\n                    <v-progress-circular\n                        v-if=\"loading\"\n                        indeterminate\n                        color=\"primary\"\n                        :size=\"15\"\n                    ></v-progress-circular>\n                </CButton>\n            </form>\n        </CCardBody>\n    </CCard>\n</template>\n\n<script>\nimport { venueAmountTypes } from \"../../constants\";\n\nexport default {\n    name: \"CreateVenueItemAmount\",\n    components: {},\n    data() {\n        return {\n            venueItem: \"\",\n            name: \"\",\n            amount: \"\",\n            type: \"\",\n            date: \"\",\n            venueItems: {\n                items: [],\n                loading: false\n            },\n            loading: false,\n            errors: {},\n            dateMenu: false,\n            venueAmountTypes: venueAmountTypes\n        };\n    },\n    watch: {\n        venueItems: [\n            function search(val) {\n                let self = this;\n                if (self.venueItems.length > 0 || self.venueItems.loading)\n                    return;\n                self.venueItems.loading = true;\n                let data = {\n                    venue_id: self.$route.params.venueId\n                };\n                this.$store\n                    .dispatch(\"venue/item/get\", data)\n                    .then(response => {\n                        self.venueItems.items = response.data;\n                        self.venueItems.loading = false;\n                    })\n                    .catch(error => {\n                        self.venueItems.loading = false;\n                    });\n            }\n        ]\n    },\n    methods: {\n        submit() {\n            let self = this;\n            if (self.loading) {\n                return;\n            }\n            self.loading = true;\n            let data = {\n                venue_item_id: self.$route.params.venueItemId,\n                venue_item: self.venueItem,\n                type: self.type,\n                amount: self.amount,\n                date: `${self.date}-01`\n            };\n            this.$store\n                .dispatch(\"venue/item/amount/create\", data)\n                .then(response => {\n                    self.loading = false;\n                    self.errors = {};\n                    self.$router.back();\n                })\n                .catch(error => {\